.btn {
    width: 120px;
    height: 28px;
    border-radius: 15px;
    outline: none;
    margin-bottom: 18px;
    background-color: khaki;
    font-size: 18px;
    box-shadow: 0 0 5px #000, inset 0 1px 8px -2px #fff;
}

.btn:hover {
    cursor: pointer;
    background-color: yellow;
}